'use client';
import { useParams } from 'next/navigation';
import { allProducts } from '@/lib/products';
import ProductCard from "@/components/ProductCard";
import { useCartStore } from '@/lib/cart';

import { useState, useEffect } from 'react';

export default function ProductDetails(){
    const params = useParams();
    const id = params.id;
    const [product, setProduct] = useState<any>(null);
    const [similarItems, setSimilarItems] = useState<any[]>([]);
    const [loading, setLoading] = useState(true);
    const { addToCart } = useCartStore();

    useEffect(() => {
        if (!id) return;
        setLoading(true);
        fetch(`/api/products/${id}`)
            .then(res => res.json())
            .then(data => {
                setProduct(data);
                if (data.category) {
                   fetch(`/api/products?category=${encodeURIComponent(data.category)}`)
                     .then(res => res.json())
                     .then(items => setSimilarItems(items.filter((p: any) => p.id !== data.id)));
                }
                setLoading(false);
            })
            .catch(() => setLoading(false));
    }, [id]);

    if (loading) return <div className="p-10 text-center">Loading product details...</div>;
    if (!product) return <div className="p-10 text-center text-red-500">Product not found</div>;

    const isOutOfStock = product.stock <= 0;


    return (
        <div className="bg-white rounded-t-3xl sm:mt-12 mt-6 mx-auto p-6">
            {/* Product Info */}
            <div className="grid md:grid-cols-2 gap-8">
                <img src={product.image} alt={product.title} className="w-full rounded-lg shadow" />
                <div>
                    <h1 className="text-3xl font-bold mb-2">{product.title}</h1>
                    <p className="text-gray-600 mb-4">{product.description}</p>
                    <p className="text-xl font-semibold text-indigo-600">৳{product.price}</p>
                    <button 
                        disabled={isOutOfStock}
                        onClick={() => addToCart(product.id)}
                        className={`mt-4 px-6 py-2 rounded transition-colors ${isOutOfStock ? 'bg-gray-400 cursor-not-allowed' : 'bg-indigo-600 text-white hover:bg-indigo-700'}`}>
                        {isOutOfStock ? 'Stock Out' : 'Add to Cart'}
                    </button>

                </div>
            </div>

            {/* Reviews */}
            <div className="mt-12">
                <h2 className="text-2xl font-semibold mb-4">Customer Reviews</h2>
                <p className="text-gray-500">({product.reviews} reviews)</p>
            </div>

            {/* Similar Items */}
            <div className="mt-12">
                <h2 className="text-2xl font-semibold mb-4">Similar Items</h2>
                <div className="grid grid-cols-1 sm:grid-cols-2 md:grid-cols-4 gap-6">
                    {similarItems.map(item => (
                        <ProductCard key={item.id} product={item} />
                    ))}
                </div>
            </div>
        </div>
    );
};
